@import "./normalize.scss";
@import "./default.scss";
@import "./fonts";
@import "./Icons.scss";
@import "./header/header.scss";
@import "./footer/footer.scss";
@import "./contacts/contacts.scss";
@import "./gallery/gallery.scss";
@import "./burger-menu.scss";
@import "./banner.scss";

.body {
  font-family: "Raleway";
  overflow-x: hidden;
  width: 100vw;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container {
  box-sizing: content-box;
  max-width: 1290px;
  padding-left: 25px;
  padding-right: 25px;
  margin-left: auto;
  margin-right: auto;
  @media (max-width: 400px) {
    padding-left: 10px;
    padding-right: 10px;
  }
}

button {
  font-family: "Raleway";
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  &.rectangle {
    font-weight: 700;
    letter-spacing: 0.02em;
    &.background {
      background: $darkGreen;
      border: 1.5px solid $darkGreen;
      padding: 17px 27px;
      color: $white;
      &:hover {
        background: $darkGreenHover;
      }
      &:active {
        background: none;
        color: $darkGreen;
      }
    }
    &.unbackground {
      background: none;
      border: 1.5px solid $darkGreen;
      color: $darkGreen;
      padding: 17px 27px;
      &:hover {
        color: $darkGreenHover;
        border-color: $darkGreenHover;
      }
      &:active {
        background: $darkGreen;
        color: $white;
      }
    }
  }
  &.circle {
    border-radius: 50%;
    &.background {
      background: $green;
      color: $white;
      &:active {
        background: $darkGreen;
      }
    }
    &.unbackground {
      border: 2px solid $darkBlue;
      color: $darkBlue;
      background: none;
      &:active {
        background: $darkGreen;
        color: $white;
        border: 2px solid $darkGreen;
      }
    }
  }
  &.text {
    border: none;
    background: none;
    font-weight: 600;
    letter-spacing: 0.01em;
    color: $gray;
    &:active {
      color: $darkGreen;
    }
  }
}

a {
  font-family: "Raleway";
  font-weight: 700;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.01em;
  color: $darkBlue;
  &:active {
    color: $darkGreen;
  }
}

.personal-discount {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: $darkGreen;
}
