.body__gallery {
  .gallery__container {
    .gallery__gallery-grid-blocks {
      display: flex;
      flex-direction: column;
      gap: 32px;
      li {
        overflow: hidden;
        display: flex;
        justify-content: space-between;
        gap: 32px;
        max-height: 556px;
        width: 100%;
        .img-block {
          width: 100%;
        }
        @media (max-width: 960px) {
          flex-direction: column;
          align-items: center;
        }
        &:nth-child(1) {
          div {
            width: 100%;
            &:nth-child(1) {
              @media (max-width: 960px) {
                max-height: 768px;

                img {
                  width: 100%;
                  max-width: none;
                  object-fit: cover;
                }
              }
            }
            &:nth-child(2) {
              height: 1000px;
              @media (max-width: 960px) {
                max-width: none;
                width: 100%;
                height: 100%;
              }
              position: relative;
              img {
                height: 100%;
                width: 100%;
              }
              @media (max-width: 960px) {
                max-height: 768px;
                height: 1000px;
                width: 100%;
                img {
                  max-width: none;
                  object-fit: cover;
                }
              }
              .text-block {
                padding-top: 77px;
                padding-left: 39px;
                padding-right: 90px;
                background-color: rgba($color: $darkGreen, $alpha: 0.97);
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                img {
                  max-width: 322px;
                  max-height: 293px;
                  position: absolute;
                  right: -16px;
                  bottom: -20px;
                }
                @media (max-width: 960px) {
                  height: 600px;
                  width: 600;
                  img {
                    max-width: none;
                    width: 100%;
                  }
                }
                &__title {
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 800;
                  font-size: 22px;
                  line-height: 27px;
                  letter-spacing: 0.01em;
                  color: #ffffff;
                }
                &__text {
                  margin-top: 22px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 500;
                  font-size: 15px;
                  line-height: 22px;
                  color: #ffffff;
                  strong {
                    letter-spacing: -0.01em;
                  }
                }
              }
            }
          }
        }
        &:nth-child(2) {
          div {
            &:nth-child(1) {
              img {
                object-fit: cover;
                object-position: 50% -80px;
                width: 100%;
                height: 100%;
                @media (max-width: 1500px) {
                  object-position: 50% 50%;
                }
              }
              .text-block {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .icon-play {
                  margin-top: 108px;
                }
                &__title {
                  max-width: 563px;
                  margin-top: 28px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 800;
                  font-size: 22px;
                  line-height: 27px;
                  letter-spacing: 0.01em;
                  color: #ffffff;
                }
                &__text {
                  margin-top: 12px;
                  max-width: 563px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 500;
                  font-size: 14px;
                  line-height: 21px;
                  text-align: center;
                  color: #ffffff;
                  margin-right: 5px;
                }
                strong {
                  letter-spacing: -0.01em;
                }
              }
            }
          }
        }
        &:nth-child(3) {
          div {
            margin-bottom: -3px;
            &:nth-child(1) {
              max-width: 409px;
              max-height: 404px;
              img {
                max-width: max-content;
                object-position: 50% 2px;
                object-fit: cover;
                width: 100%;
                height: 103%;
              }
            }
            &:nth-child(2) {
              max-width: 409px;
              max-height: 404px;
              img {
                object-position: 50% 27%;
                object-fit: cover;
                width: 100%;
                height: 100%;
              }
            }
            &:nth-child(3) {
              max-width: 409px;
              max-height: 404px;
              img {
                max-width: max-content;
                object-position: -85px -15px;
                object-fit: cover;
              }
              .text-block {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .icon-play {
                  margin-top: 22px;
                }
                &__title {
                  max-width: 563px;
                  margin-top: 25px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 800;
                  font-size: 22px;
                  line-height: 27px;
                  letter-spacing: 0.01em;
                  color: #ffffff;
                }
                &__text {
                  margin-top: 13px;
                  max-width: 363px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 500;
                  font-size: 14px;
                  line-height: 21px;
                  text-align: center;
                  color: #ffffff;
                  margin-right: 5px;
                }
                strong {
                  letter-spacing: -0.01em;
                }
              }
            }
          }
        }
        &:nth-child(4) {
          width: 100%;
          div {
            &:nth-child(1) {
              img {
                max-width: 520px;
                object-fit: cover;
                object-position: 65% 1px;
                height: 100%;
              }
            }
            &:nth-child(2) {
              width: 100%;
              overflow: hidden;
              img {
                overflow: hidden;
                max-width: 739px;
                object-fit: cover;
                object-position: 65% -250px;
                width: 100%;
                height: 100%;
                @media (max-width: 1500px) {
                  object-position: 20% 10%;
                }
              }
            }
          }
        }
        &:nth-child(5) {
          margin-bottom: -3px;
          max-width: max-content;
          max-height: 570px;
          height: 100%;
          div {
            &:nth-child(1) {
              img {
                object-fit: cover;
                object-position: 50% -78px;
                width: 100%;
                height: 100%;
                @media (max-width: 1500px) {
                  object-position: 50% 50%;
                }
              }
              .text-block {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .icon-play {
                  margin-top: 108px;
                }
                &__title {
                  max-width: 563px;
                  margin-top: 28px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 800;
                  font-size: 22px;
                  line-height: 27px;
                  letter-spacing: 0.01em;
                  color: #ffffff;
                }
                &__text {
                  margin-top: 12px;
                  max-width: 563px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 500;
                  font-size: 14px;
                  line-height: 21px;
                  text-align: center;
                  color: #ffffff;
                  margin-right: 5px;
                }
                strong {
                  letter-spacing: -0.01em;
                }
              }
            }
          }
        }
        &:nth-child(6) {
          div {
            &:nth-child(1) {
              max-width: 409px;
              max-height: 404px;
              img {
                max-width: max-content;
                object-position: 50% 2px;
                object-fit: cover;
                width: 100%;
                height: 103%;
              }
            }
            &:nth-child(2) {
              max-width: 409px;
              max-height: 404px;
              img {
                object-position: 50% 27%;
                object-fit: cover;
                width: 100%;
                height: 100%;
              }
            }
            &:nth-child(3) {
              max-width: 409px;
              max-height: 404px;
              img {
                max-width: max-content;
                object-position: -85px -15px;
                object-fit: cover;
              }
              .text-block {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                .icon-play {
                  margin-top: 22px;
                }
                &__title {
                  max-width: 563px;
                  margin-top: 25px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 800;
                  font-size: 22px;
                  line-height: 27px;
                  letter-spacing: 0.01em;
                  color: #ffffff;
                }
                &__text {
                  margin-top: 13px;
                  max-width: 363px;
                  font-family: "Montserrat";
                  font-style: normal;
                  font-weight: 500;
                  font-size: 14px;
                  line-height: 21px;
                  text-align: center;
                  color: #ffffff;
                  margin-right: 5px;
                }
                strong {
                  letter-spacing: -0.01em;
                }
              }
            }
          }
        }
      }
      .img-block-big {
        overflow: hidden;
        img {
          object-position: 0 -247px;
          @media (max-width: 1500px) {
            object-position: 100% 100%;
            max-width: fit-content;
          }
        }
      }
      .img-text-block {
        background-color: $darkGreen;
        max-width: 520px;
        width: 100%;
      }
      .img-block {
      }
      .video-block {
        .icon-play {
          border: 2px solid $white;
          border-radius: 50%;
          width: 61px;
          height: 61px;
          display: flex;
          justify-content: center;
          align-items: center;
          &::before {
            margin-left: 5px;
            font-size: 20px;
            color: $white;
          }
          &:hover {
            border-color: rgba($color: #eeeeee, $alpha: 1);
            &::before {
              color: rgba($color: #eeeeee, $alpha: 1);
            }
          }
        }
        position: relative;
        max-height: 554px;
        height: 100%;
        overflow: hidden;
        .text-block {
          text-align: center;
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background-color: rgba(0, 0, 0, 0.4);
          cursor: pointer;
          &:hover {
            background-color: rgba(0, 0, 0, 0.521);
          }
        }
      }
      .img-block-small {
        max-width: 409px;
        max-height: 404px;
        overflow: hidden;
        img {
          object-position: 50% 50%;
        }
      }
      img {
      }
      .text-block {
      }
    }
    .unbackground {
      margin-top: 70px;
      display: block;
      margin-left: auto;
      margin-right: auto;
      padding-left: 40px;
      padding-right: 40px;
      &:hover {
      }
      &:active {
      }
      &:focus {
      }
    }
  }
}
