@import "./default.scss";

.social {
  display: flex;
  align-items: center;
  color: #323232;

  &__facebook {
    font-size: 12px;
    margin-left: 3px;
    &:hover {
      color: $green;
    }
  }
  &__instagram {
    font-size: 13px;
    margin-left: 21px;
    &:hover {
      color: $green;
    }
  }
  &__youtube {
    font-size: 12px;
    margin-left: 20px;
    &:hover {
      color: $green;
    }
  }
}
