@import "../default.scss";
@import "./logo.scss";
@import "../social.scss";
@import "./social-buttons.scss";
@import "./menu.scss";

.header {
  .social-button {
    @media (max-width: $tablet) {
      display: none;
    }
  }
  .top {
    padding-top: 11px;
    padding-bottom: 11px;
    background: rgba(245, 245, 245, 0.5);
    .top__container {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      .social {
        @media (max-width: $mobile) {
          display: none;
        }
      }
      .top__info {
        .phone {
          display: none;
          align-items: center;
          font-family: "Montserrat";
          font-style: normal;
          gap: 7px;
          font-weight: 600;
          font-size: 12px;
          line-height: 15px;
          .icon-phone {
            color: $darkGreen;
            font-size: 10px;
          }
          .green {
            color: $darkGreen;
            margin-right: -2.2px;
          }
          &:hover {
            color: $darkGreenHover;
            .icon-phone {
              color: $darkGreenHover;
            }
            .green {
              color: $darkGreenHover;
            }
          }
          @media (max-width: $mobile) {
            display: inline-flex;
          }
        }
        .tel-me {
          display: none;
          font-family: "Raleway";
          font-style: normal;
          font-weight: 600;
          font-size: 13px;
          line-height: 15px;
          letter-spacing: 0.01em;
          color: $darkGreen;
          border-bottom: 1px solid $darkGreen;
          &:hover {
            color: $darkGreenHover;
          }
          @media (max-width: $mobile) {
            display: inline-block;
          }
        }
        .personal-discount {
          margin-left: 180px;
          @media (max-width: $tablet) {
            margin-left: auto;
          }
          @media (max-width: $mobile) {
            display: none;
          }
        }
        @media (max-width: $mobile) {
          display: flex;
          justify-content: space-between;
          gap: 25px;
        }
      }
      .interface {
        margin-top: -2px;
        display: flex;
        align-items: center;
        gap: 44px;
        &__user {
          display: flex;
          align-items: center;
          gap: 27px;
          @media (max-width: 700px) {
            display: none;
          }
        }
        &__language {
          font-family: "Raleway";
          font-style: normal;
          font-weight: 500;
          font-size: 13px;
          line-height: 15px;
        }
        .language__custom-select {
          .select-selected {
            &:hover {
              background: none;
            }
          }
          .select-items div,
          .select-selected {
            border: none;
          }
        }
        .user__login {
          display: flex;
          gap: 7px;
          align-items: baseline;
          &:hover {
            margin-top: 2px;
            border-bottom: 2px solid $green;
            .icon-user {
              color: $darkGreen;
            }
          }
          .icon-user {
            color: $green;
            font-size: 11px;
          }
          &-text {
            font-weight: 500;
            font-size: 13px;
            line-height: 15px;
            letter-spacing: 0.02em;
            color: #323232;
          }
        }
        .user__registration {
          font-weight: 500;
          font-size: 13px;
          line-height: 15px;
          letter-spacing: 0.02em;
          padding-bottom: 1px;
          &:hover {
            margin-top: 2px;
            border-bottom: 2px solid $green;
          }
          color: #323232;
        }
      }
    }
  }
  .header-main-block {
    padding-top: 17px;
    @media (max-width: 400px) {
      padding-top: 1px;
    }
    &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .header-main-block__burger-menu {
        display: none;
        @media (max-width: $tablet) {
          display: block;
        }
      }
      .header-main-block__interface {
        max-width: 59%;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media (max-width: 1250px) {
          max-width: 65%;
        }
        @media (max-width: $tablet) {
          max-width: max-content;
        }
      }
      .header-main-block__contacts-block {
        flex: 0 0 35.5%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        @media (max-width: 700px) {
          display: none;
        }
      }
      .telephone-block {
        white-space: nowrap;
        display: flex;
        flex-direction: column;
        gap: 3px;
        margin-top: -6px;
        &__link {
          font-family: "Montserrat";
          letter-spacing: -0.005em;
          font-weight: 600;
          font-size: 13px;
          line-height: 16px;
          display: block;
          .dark-green {
            color: $darkGreen;
          }
        }
        @media (max-width: $tablet) {
          margin-right: 20px;
        }
      }
      .bag-block {
        margin-bottom: 5px;
        display: flex;
        &__icon-bag {
          font-size: 24px;
          color: $darkGreen;
          &:hover {
            color: $darkGreenHover;
          }
          @media (max-width: 400px) {
            font-size: 23px;
          }
        }
        &__counter {
          margin-left: -10px;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: $green;
          &::after {
            content: "0";
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 600;
            font-size: 8px;
            line-height: 10px;
            letter-spacing: 0.02em;
            color: $white;
          }
        }
      }
    }
    .header-main-block__last-block {
      display: flex;
      flex: 0 0 52%;
      justify-content: space-between;
      align-items: center;
      @media (max-width: $tablet) {
        justify-content: end;
        flex: auto;
      }
      .button-block {
        margin-bottom: 4px;
        display: flex;
        gap: 14px;
        align-items: center;
        .header-main-block__button {
          font-family: "Raleway";
          font-weight: 500;
          font-size: 13px;
          line-height: 15px;
          letter-spacing: 0.04em;
          padding: 12px 18px 13px 22px;
        }
        @media (max-width: $tablet) {
          margin-right: 20px;
        }
        @media (max-width: $mobile) {
          display: none;
        }
      }
      .header-main-block__text {
        font-family: "Montserrat";
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        letter-spacing: 0.02em;
        color: $gray;
        @media (max-width: 1100px) {
          display: none;
        }
      }
    }
  }
  .header-bottom {
    hr {
      border: 0.2px solid rgba(0, 0, 0, 0.05);
    }
    &__personal-discount {
      padding-left: 4px;
      font-size: 13px;
      text-align: center;
    }
  }
}
