@import "../default.scss";

.header-menu-container__menu {
  display: flex;
  gap: 55.5px;
  justify-content: center;
  font-family: "Raleway";
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.02em;
  @media (max-width: 1200px) {
    gap: 30px;
  }
  @media (max-width: 1076px) {
    gap: 10px;
  }
  @media (max-width: 973px) {
    gap: 7px;
  }
  @media (max-width: $tablet) {
    display: none;
  }
  .menu__item {
    text-align: center;
    margin-top: 38px;
    padding-left: 7px;
    padding-right: 6.5px;
    padding-bottom: 12px;
    cursor: pointer;
    &:hover {
      color: $darkGreen;
      padding-bottom: 9px;
    }
    &:active {
      border-bottom: 3px solid $darkGreen;
    }
  }
}
